import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Modal from '@mui/material/Modal';
import domain from '../../../config';
import styles from './modalFile.module.scss';
import axios from 'axios';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogTitle from '@mui/material/DialogTitle';
import { useState } from 'react';
import fileDownload from 'js-file-download'


const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 420,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4
};
interface Props {
  token : string  | undefined ;
  open : boolean ;
  setOpen : React.Dispatch<React.SetStateAction<boolean>> ;
  imgInfo : string ;
  imgStats : {size : number , date : string } ;
  removeDeletedImage(imgInfo : string) : void;
  setRefresh: React.Dispatch<React.SetStateAction<boolean>> ;
}


const ModalFile:React.FC<Props> = ({
  token,
  open,
  setOpen,
  imgInfo,
  imgStats,
  removeDeletedImage,
  setRefresh
}) => {
  const deleteFileButton = () : void => {
    handleClickOpenPropmpt();
  };

  const [openPrompt, setOpenPrompt] = useState<boolean>(false);

  const handleClickOpenPropmpt = () : void => {
    setOpenPrompt(true);
  };

  const handleClosePrompt = async () :  Promise<void> => {
    setOpenPrompt(false);
  };

  const deleteFileAction = async () : Promise<void> => {
    await axios.delete('/api/v1/images', {
      headers: {
        authorization: 'Bearer' + ' ' + token
      },
      data: { img: imgInfo }
    })
    setRefresh(state => !state)
    await handleClosePrompt();
    setOpen(false);
    removeDeletedImage(imgInfo);
  };

 const downloadImage = async() =>{
 axios({url : `/api/v1/images/${imgInfo}`, method: "GET",responseType :"blob" }).then((res)=>{
  fileDownload(res.data,imgInfo)
 })
 }
  return (
    <div>
      <Modal
        open={open}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
            IMAGE INFO
          </Typography>
          <img className={styles.imgModalFile} src={`${domain}/${imgInfo}`} />
          <Typography id="modal-modal-description" sx={{ mt: 2 }}>
            <div>Name : {imgInfo}</div>
            {imgStats ? (
              <>
                <div>Size : {imgStats.size} KB</div>
                <div>Uploaded : {imgStats.date}</div>
              </>
            ) : (
              ''
            )}

            <div>
              <button
                onClick={deleteFileButton}
                className={styles.deleteButton}
              >
                Delete
              </button>{' '}
               (Image will be Deleted)
            </div>
            <div><button className={styles.downloadButton} onClick={downloadImage}>Donwload</button> (Download from Server)</div>
            
          </Typography>
          <button className={styles.closeButton} onClick={() => setOpen(false)}>
            Close
          </button>
        </Box>
      </Modal>

      <div>
        <Dialog
          open={openPrompt}
          onClose={handleClosePrompt}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">
            "Are you sure you want to delete this image ?"
          </DialogTitle>
          <DialogActions>
            <Button onClick={handleClosePrompt}>Disagree</Button>
            <Button onClick={deleteFileAction} autoFocus>
              Agree
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    </div>
  );
};

export default ModalFile;
