import { useEffect, useState } from 'react';
import { getData } from '../FetchFunctions/fetchFunctionsAdmin/FetchFunctionsAdmin';
import styles from './fileManager.module.scss';
import domain from '../../config';
import { LazyLoadImage } from 'react-lazy-load-image-component';
import ModalFile from './modalFile/modalFile';
import { sendData } from '../FetchFunctions/fetchFunctionsAdmin/FetchFunctionsAdmin';
import { useContext } from 'react';
import { TaskContext } from '../../components/Context/TaskContext';
import axios from 'axios';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import ImagesSize from './imagesSize/ImagesSize';
import { useNavigate } from 'react-router-dom';

const FileManager :React.FC = () => {
  const [data, setData] = useState<string[]>([]);
  const [open, setOpen] = useState<boolean>(false);
  const [results,setReuslts] = useState<string[]>([])
  const [imgStats, setImgStats] = useState({size : 0 , date : 'no data'});
  const { token } = useContext(TaskContext);
  const [imgInfo, setImgInfo] = useState<string>('no data');
  // const [isMoreNeeded,setIsMoreNeeded] = useState<boolean>(true)
  const [isFormOpen,setIsFormOpen] = useState<boolean>(false);
  const [image,setImage] = useState<any>('');
  const [triggerAnimation,setTriggerAnimation] = useState<boolean>(false)
  const [disableSubmit,setDisableSubmit] = useState<boolean>(false);
  const [refresh,setRefresh] = useState<boolean>(false);
  const navigate = useNavigate();

  const navigateToLogs = () : void => {
    navigate(`/Admin/Logs`);
  };


  const handleOpen = async (el : string) : Promise<void> => {
    
    await sendData({ img: el }, '/api/v1/images', token).then(data =>
      setImgStats(data.data)
    );
    setImgInfo(el); 
    setOpen(true);
  };

  useEffect(() => {
    getData('/api/v1/images').then(data => 
      setData(data.data.files) 
      
      );
  }, []);

  // const loadAll = async() : Promise<void> =>{
  //   setData(results)
  //   setIsMoreNeeded(false)
  // }

  const removeDeletedImage = (imgInfo : string) : void => {
    const index = data.indexOf(imgInfo);
    if (index > -1) {
      data.splice(index, 1);
      setData(data);
    }
  };
 
  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) =>{
    e.preventDefault(); 
    if(!image){
      alert('Please Upload Image');
      return;
    }
    const datas = new FormData();
    datas.append('img', image);
     const config = {
      headers: { authorization: 'Bearer' + ' ' + token }
    };

    axios.post('/api/v1/mycloud', datas, config)
    
    setTimeout(()=>{setData((state : string[] ) => ([
      ...state,
      image.name
    ]));
    // setReuslts((state : string[] ) => ([
    //   ...state,
    //   image.name
    // ]));
   },200)
    
    setRefresh(state=> !state)
    setTriggerAnimation(true)
    setDisableSubmit(true);
    setTimeout(()=>{
      setTriggerAnimation(false)
      setDisableSubmit(false);
    },4000) 
  }


  return (
    <>
      <div className={styles.fileManagerTitle}>FILE MANAGER <span className={styles.imageSize}><ImagesSize refresh={refresh} /><span className={styles.logs} onClick={navigateToLogs}>History</span></span></div>
     
      <div className={triggerAnimation ? styles.alertMessage : styles.noAlertMessage}> <Alert severity="success">
        <AlertTitle>Success</AlertTitle>
        Image Uploaded
      </Alert></div>
      <div>
        <button className={styles.addItem} onClick={()=>setIsFormOpen(state => !state)}>{isFormOpen ? 'Close' : 'Add New File'}</button>
        </div>
      <div className={isFormOpen ? styles.addFileForm : styles.noloadAll} >
        <form onSubmit={handleSubmit}>
          <div id={'upload'} >
            <input  type='file' className={
                image ? styles.customfileinput : styles.customfileinputempty
              } onChange={(e:any) => setImage(e.target.files[0])}></input><div className={styles.imageDescription}>(Up to 10mb and no conversion of format )</div>
              <input className={image ? styles.yesImage : styles.noImage} disabled={disableSubmit} type="submit" value={image ? "Send" : 'Please Upload Image'}></input>
              </div>
              </form>
              </div>

      <div className={styles.fileManagerWrapper}>
        {data.map((el,i) => {
          console.log(el)
          return (
            <div key={el} className={styles.imageWrapper}>
              <div className={styles.imageIndex}  onClick={() => handleOpen(el)}>
                <LazyLoadImage
                  effect="blur"
                  className={styles.image}
                  src={`${domain}/${el}`}
                />
                <div className={styles.ImageName}>
                  See Info <div>{el}</div>{' '}
                </div>
              </div>
            </div>
          );
        })}
      </div>
     
     
      {/* <button className={isMoreNeeded ? styles.loadAll :styles.noloadAll } onClick={loadAll}>Load More</button> */}
      <ModalFile
        token={token}
        open={open}
        setOpen={setOpen}
        imgInfo={imgInfo}
        imgStats={imgStats}
        removeDeletedImage={removeDeletedImage}
        setRefresh={setRefresh}
      />
      ;
    </>
  );
};
export default FileManager;
