
import styles from './imagesSize.module.scss'
import { useEffect, useState } from 'react';
import { getData } from '../../FetchFunctions/fetchFunctionsAdmin/FetchFunctionsAdmin';
interface Props {
  refresh : boolean
}

const ImagesSize :React.FC<Props> = ({refresh}) => {
  const [size, getSize] = useState<number>(0);
 useEffect(()=>{
  setTimeout(()=>{
    getData('/api/v1/images/get/all').then(data=> getSize(data.data.folder))
  },500)  
 },[refresh])


  return <div className={styles.imagesSize}>Used :{(size/1000000).toFixed(2)}  MB</div>;
};

export default ImagesSize;
